@import url(https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: calc(.4rem + 1vw);
}
:root{
  --gray: #333;
  --softgray: #eee;
}
.container{
  min-height: 100vh;
}
.page{
  position: absolute;
  overflow: hidden;
  height: 90%;
  width: 100%;
}
.page-enter {
  opacity: 0;
  -webkit-transform: scale(.95) translateX(-10%);
          transform: scale(.95) translateX(-10%);
}

.page-enter-active {
  opacity: 1;  
  -webkit-transform: scale(1) translateX(0%);  
          transform: scale(1) translateX(0%);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;  
}

.page-exit {
  -webkit-transform: scale(1) translateX(0%);
          transform: scale(1) translateX(0%);
  opacity: 1;
}

.page-exit-active {
  opacity: 0;
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
  -webkit-transform: translateX(-10%);
          transform: translateX(-10%);
}
/* nav */
nav {
    background-color: #f5f5f5af;
    box-shadow: 0 0 3px #c3c3c3,0 0 5px #e4e4e4;
    color: var(--gray);
    height: 10%;
    position: relative;
    display: -webkit-flex;
    display: flex;
    place-items: center;
    width: 100%;
    position: relative;
    -webkit-justify-content: center;
            justify-content: center;
}
/* .logo-wrap{
} */
.logo{
    font-family: 'New Tegomin', serif;
    font-size: calc(1rem + 1vw);
    font-weight: bold;
    padding: 0;
    margin: 1vw;
    vertical-align: super;
    -webkit-text-decoration: 0;
            text-decoration: 0;
    color: black;

}
.bold{
    font-weight: bold;
}
nav .menu a {
    -webkit-text-decoration: 0;
            text-decoration: 0;
    padding: 0vw;
    margin: 0 1vw;
    text-transform: capitalize;
    font-size: 1rem;    
}
nav .menu a:hover {
    color: black;
}
.menu a {
    color: #333;

}
.serach-box{
    -webkit-flex: 1 1;
            flex: 1 1;
}
.serach-box input {
    padding: .05vw 0vw;
    outline: 0;
    margin: 0 auto;
    float: right;
    border: 1px solid rgb(236, 236, 236);
    box-shadow: 0 0 .05vw #333;
    color: #333;
    border-radius: .5vw;
    background: transparent;
    width: 8rem;
    margin: 0 1vw;
    font-size: .625rem;

}
input[type="placeholder"]{
    font-size: 10px;
}
.table-wrap{
    overflow-y: scroll;
    overflow-x: auto;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 0 auto;
    height: 70vh;
    width: 80%;
}
/* table */
.top100-table{
    position: relative;
    height: 100%;
    width: 100%;
    border-collapse: collapse;
  }
.top100-table img {
    width: auto;
    max-height: 2rem;
    max-width: 2rem;
    height: 100%;
    margin: auto .5vw auto 0 ;
}
  table .tr-wrap{
    color: #333;
    border-bottom: 1px solid rgb(216, 216, 216);
    text-transform: uppercase;
  }
  table .tr-wrap:hover{
    background: #eee;
  }
  .top100-table .coin-name-td{
    min-width: 100px;
    width: 200px;
    max-width: calc(1rem + 1vw);
    position: relative;
    height: 5vh;
    padding: 0 1vw;
    background: #eee;
  }

  .top100-table a {
    cursor: pointer;
    -webkit-text-decoration: 0;
            text-decoration: 0;
    color: #333;
    position: relative;
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 100%;
    top: 0;
    margin: .25vw;
  }
  .card-flex{
    display: -webkit-flex;
    display: flex;
    width: 100%;
    position: a;
  }
  .coin-mini-details{
    display: -webkit-flex;
    display: flex;
  }
  .coin-mini-details p {
    margin: 0 .1vw;
    font-size: 1.5vw;
  }
  .card-flex h5{
    font-size: 1vw;
  }
  .card-name{
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .top100-table .rank{
    padding: .25vw .5vw;
    border-radius: .25vw;
    font-size: calc(.5rem + .5vw);
    background-color: #333;
    position: relative;
    top: 0;
    color: #eee;
  }
.homepage{
    position: relative;
}
.global-data-section{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    text-align: center;
    background-color: #f5f5f5af;
    color: #333;
    margin: 1vw auto;
    border-radius: 1vw;
    width: 80%;
}
.details-num-card{
    padding: 1vw;
}
.details-num-card h1{
    font-size: 1.65rem;
    color: rgb(37, 173, 37);
}
.details-num-card h1 span{
    font-size: 1.5rem;
    color: #333;
}
.spinner{
    position: relative;
    margin: 2rem auto;
    border-top: 2px solid transparent;
    border-left: 2px solid black;
    border-bottom: 2px solid transparent;
    -webkit-animation: spinner .75s linear infinite;
            animation: spinner .75s linear infinite;
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
}
@-webkit-keyframes spinner {
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes spinner {
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
} 
